<template>
  <div class="evaluate">
    <mt-header class="header">
      <a slot="left"  @click="back">
        <mt-button icon="back" v-if="type == 1">月嫂评价</mt-button>
        <mt-button icon="back" v-else>护工评价</mt-button>
      </a>
      <mt-button slot="right" @click.native="showAllDate">{{date}}</mt-button>
    </mt-header>
    <div class="evaluateBody">
      <div class="evaluateBodyItem" v-for="(t, index) in tpl">
        <p>{{index}}、{{t}}</p>
        <div class="imgs">
          <img :ref='t+1' data-index="1" src="../assets/ass.png" alt="" @click="handleClick($event, t)">
          <img :ref='t+2' data-index="2" src="../assets/ass.png" alt="" @click="handleClick($event, t)">
          <img :ref='t+3' data-index="3" src="../assets/ass.png" alt="" @click="handleClick($event, t)">
          <img :ref='t+4' data-index="4" src="../assets/ass.png" alt="" @click="handleClick($event, t)">
          <img :ref='t+5' data-index="5" src="../assets/ass.png" alt="" @click="handleClick($event, t)">
        </div>
      </div>
      <div class="comment">
        <a class="w_cell" style="min-height:1rem;">
          <div class="w_cell_wraper">
            <div class="w_cell_title" style="flex: 2;">
              <span class="w_cell_text">综合评定</span>
            </div>
            <div class="w_cell_value" style="border:none;"></div>
          </div>
        </a>
        <a class="w_cell">
          <div class="w_cell_wraper">
            <textarea v-model="comment" class="w_field_core" placeholder="" style="border-radius:0;margin-bottom: 0.2rem;"  rows="5"></textarea>
          </div>
        </a>
      </div>
    </div> 
    <div class="footer">
      <mt-button size="large" @click.native="handleNextClick">确认</mt-button>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'evaluate',
  data () {
    return {
      type: null,
      code: null,
      careId: null,
      tpl: [],
      assessments: [],
      comment: ''
    }
  },
  methods: {
    ...mapActions({
      getMoonMomAssessTpls: 'getMoonMomAssessTpls',
      getCareAssessTpls: 'getCareAssessTpls',
      assessmentsCare: 'assessmentsCare'
    }),
    back: function() {
      history.go(-1);
    },
    handleNextClick: function() {
      let obj = {};
      obj.code = this.code;
      obj.careId = Number(this.careId);
      obj.assessments = this.assessments;
      obj.comment = this.comment;
      this.assessmentsCare(obj).then(response => {
        if(response.ok) {
          history.go(-1);
        }
      })

    },
    handleClick: function(e,t) {
      let obj = {};
      let repeat = false; 
      let indexNum = null;
      obj[t] = e.target.getAttribute('data-index');

      this.assessments.forEach((i,index) => {
        Object.keys(i).forEach(j => {
          if(j == t) {
            repeat = true;
            indexNum = index;
            return;
          }
        })
      })

      if(repeat) {
        this.assessments[indexNum] = obj;
      } else {
        this.assessments.push(obj);
      }

      for(let i = 1; i <= 5; i++) {
        this.$refs[t+i][0].setAttribute('src', '/static/ass.png');
      }
      for(let i = 1; i <= e.target.getAttribute('data-index'); i++) {
        this.$refs[t+i][0].setAttribute('src', '/static/assed.png');
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.code = to.query.code;
      vm.careId = to.query.careId;
      vm.type = to.query.type;
      if(to.query.type == 1) {
        vm.getMoonMomAssessTpls(-1).then(response => {
          if(response.ok) {
            vm.tpl = response.data.tpl;
          }
        })
      } else {
        vm.getCareAssessTpls(-1).then(response => {
          if(response.ok) {
            vm.tpl = response.data.tpl;
          }
        })
      }
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.evaluate {
  font-size: 0.4rem;
  color: #565256;
}
.evaluateBodyItem, .comment{
  padding: 0.5rem;
}
.comment {
  margin-bottom: 1rem;
}
.evaluate .imgs {
  margin: 0.5rem 0rem 0rem 0.3rem;
}
.evaluate .imgs img {
  width: 10%;
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
}
.footer .mint-button {
  height: 1rem;
}
</style>



